/* VARIABLES */
:root{
    --color-primary: #202F66;
    --color-secondary: #6ed5b8;
}

/* FUENTES */
@font-face {
    font-family: 'Georgia';
    src: url('/fonts/georgia/georgia.ttf');
}

@font-face {
    font-family: 'Futura';
    src: url('/fonts/futura/FuturaCyrillicMedium.ttf');
}

/* ESTILOS GENERALES */
html{
    height: 100%;
    font-size: 11px;
}

body{
    width: 100%;
    height: 100%;
    font-size: 1.6rem;
}


/* ESTRUCTURA Y LAYOUT */
.layout{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    background: linear-gradient(#fff, var(--color-primary) 65%);
}
/* HEADER */
.header{
    width: 80%;
    position: fixed;
    height: 75px;
    margin-top: 1.5rem;
    margin-bottom: 3rem;
    background: var(--color-secondary);
    border-radius: 3rem;
    -webkit-border-radius: 3rem;
    -moz-border-radius: 3rem;
    -ms-border-radius: 3rem;
    -o-border-radius: 3rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.header__logo{
    justify-content: start;
}

.header__img{
    height: 5rem;
    margin-left: 1rem;
}

/* MAIN */
.main{
    width: 85%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 16rem 1rem;
}

.main__intro{
    text-align: center;
    margin-top: 2rem;
}

.main__heading{
    font-size: 4.5rem;
    line-height: 6rem;
    color: #fff;
    font-family: 'Georgia', sans-serif;
}

.main__heading span{
    font-family: 'Futura';
    font-size: 6rem;
    color: var(--color-secondary);
}

.main__text{
    font-family: 'Futura', sans-serif;
    color: #fff;
    margin-top: 2rem;
}

.main__contact{
    padding: 2rem;
}

.main__button{
    width: 15rem;
    height: 5rem;
    background: var(--color-secondary);
    font-family: 'Futura';
    font-weight: bold;
    color: var(--color-primary);
    border: none;
    border-radius: .5rem;
    -webkit-border-radius: .5rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    cursor: pointer;
    transition: all .3s ease-in-out;
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
}

.main__button:hover{
    background: #fff;
    border: 1px solid var(--color-secondary);
    color: var(--color-secondary);
}

/* FOOTER */
.footer{
    width: 100%;
    padding: 4rem .5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background: #fff;
    color: var(--color-primary);
}

.footer__branding{
    padding: 1rem;
}

.footer__copyright,
.footer__address,
.footer__postal-code,
.footer__country,
.footer__policy{
    font-size: 1.5rem;
    font-family: 'Futura';
}

.footer__contact-info{
    padding: .5rem;
}

.footer__legal{
    padding: .5rem;
}

.footer__heading{
    font-size: 2.5rem;
    padding-top: 5rem;
    padding-bottom: 2rem;
}

.footer__policy{
    text-decoration: underline;
}

.footer__social{
    padding: .5rem;
}

.footer__social-list{
    height: 6rem;
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer__social-link{
    margin: 0 1rem;
}

.footer__social-link img{
    width: 2.5rem;
}

.footer__logo-title{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: var(--color-primary);
}

.footer__logo{
    width: 20rem;
    height: 8rem;
    top: 0;
}

.footer__logo-bar{
    top: 0;
}

/* MOBILE */
@media (max-width: 592px){
    
    .main__heading {
        font-size: 3rem;
        line-height: 4rem;
    }

    .main__heading span{
        font-size: 4rem;
    }

    .footer{
        width: 100%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        padding: 4rem .5rem;
    }
}